// ===== VARIABLES =====
$min-width: 320px;
$max-width: 1920px;

// Media Breakpoints
$grid-breakpoints: (
        xs: 0,
        min: 321px, // Min width
        xss: 370px, // iPhone SE
        xsm: 390px, // iPhone 14
        xsl: 420px, // iPhone Plus/Max
        sm: 572px,
        md: 768px,
        lg: 992px,
        xl: 1280px,
        xxl: 1440px
) !default;

// Paths
$img: '../img';
$fonts: '../fonts';

// Fonts
$font-default: 'Rubik', sans-serif;
$font-secondary: 'Inter', 'Rubik', sans-serif;

// Colors
$color-black: #000;
$color-chinese: #0d1217;
$color-darker: #141619;
$color-dark: #20242a;
$color-dark-gray: #394351;
$color-gray: #687283;
$color-medium-gray: #79798f;
$color-charcoal-gray: #8c94a0;
$color-charcoal-light: #d7dadf;
$color-mist: #ebecf0;
$color-neutral: #f5f5fe;
$color-white: #fff;

$color-red: #e53e3e;
$color-blue: #4141e7;
$color-green: #38a169;
$color-navy: #1b1b96;
$color-charcoal-blue: #465366;
$color-lavender: #d2d2f8;

// Transition Settings
$bezier: cubic-bezier(.250, .460, .450, .940);
$easing: ease;
$effect: .3s $easing;
$effect-s: .15s $easing;
$effect-l: .6s $easing;

// Mixins
@mixin pseudo {
  content: '';
  display: block;
  overflow: hidden;
}

@mixin clearfix {
  content: '';
  display: table;
  clear: both;
}

@mixin font-smooth {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@mixin user-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@mixin user-drag {
  -webkit-user-drag: none;
  user-drag: none;
}
