@use '../base/var' as *;
@use '../base/breakpoints' as *;

/* ===== TOOLS ===== */
.tools {
  padding: 60px 0;
  @include media-breakpoint-up(md) {
    padding: 80px 0;
  }
  @include media-breakpoint-up(xl) {
    padding: 100px 0;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    @include media-breakpoint-up(md) {
      flex-direction: row;
      flex-wrap: wrap;
    }
  }

  &__entry {
    @include media-breakpoint-down(sm) {
      padding-right: 5px;
    }
    @include media-breakpoint-up(md) {
      flex-basis: calc(50% - 10px);
      font-size: 16px;
      line-height: 1.5;
    }
    @include media-breakpoint-up(lg) {
      flex-basis: calc(33.3% - 13.33px);
    }

    h3, .h3 {
      margin-bottom: 12px;
      @include media-breakpoint-down(md) {
        font-size: 24px;
        line-height: 32px;
      }
    }

    *:last-child {
      margin-bottom: 0;
    }
  }

  &__icon {
    margin-bottom: 24px;
    display: block;
  }

  &.has-animation {
    .head {
      opacity: 0;
      transform: translateY(30px);
      transition: transform 1s ease, opacity 1s ease;
    }

    .tools__entry {
      opacity: 0;
      transform: translateY(30px);
      transition: transform .8s .4s ease, opacity .8s .4s ease;

      &:nth-child(2) {
        transition-delay: .7s;
      }

      &:nth-child(3) {
        transition-delay: 1s;
      }
    }

    &.animated {
      .head, .tools__entry {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }
}